import { Station, Loc } from "../lib/types";
import { Map, AdvancedMarker, useMap, Pin, MapControl, ControlPosition } from '@vis.gl/react-google-maps';
import { getHighlightColor } from "../lib/colors";
import FollowTheSignsIcon from '@mui/icons-material/FollowTheSigns';
import { Box, Paper, FormControlLabel, Switch, CircularProgress, Stack } from "@mui/material";
import { useEffect, useState } from "react";

interface MapViewProps {
  stations: Station[];
  initialLocation: Loc;
  setIsInFollowMode: (b: boolean) => void;
  isInFollowMode: boolean;
  isLoading: boolean;
}

const MapView = ({ isLoading, stations, initialLocation, setIsInFollowMode, isInFollowMode }: MapViewProps) => {
  const map = useMap();

  const [checked, setChecked] = useState(false);

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) =>
    setChecked(event.target.checked);

  useEffect(() => {
    setIsInFollowMode(checked);
  }, [checked, setIsInFollowMode])

  const FollowModeCtrl = () => {
    return <Paper sx={{ p: 1, alignContent: "center", verticalAlign: "middle" }}>
      <Stack direction="row">
        <FormControlLabel
          control={<Switch
            checked={checked}
            onChange={handleChange}
          />}
          label={<FollowTheSignsIcon />}
        />
        <Box component="span" sx={{ width: 45, minHeight: 45, height: 45, minWidth: 45 }}>
          {isLoading && <CircularProgress
            disableShrink
            sx={{ mt: 1 }}
            variant="indeterminate"
            size={20} />}
        </Box>
      </Stack>
    </Paper>;
  }

  const maxNumEBikes = stations.reduce((acc, station) => Math.max(acc, station.numEBikes), 0);

  return <Map
    mapTypeControl={false}
    streetViewControl={false}
    mapId="map"
    defaultCenter={initialLocation}
    defaultZoom={15} >
    <MapControl position={ControlPosition.TOP_LEFT}>
      <Box sx={{ p: 1.5 }}>
        <FollowModeCtrl />
      </Box>
    </MapControl>
    {stations.length && map && stations.map((station, index) => {
      const background = getHighlightColor({
        station,
        scale: { high: "#2e7d32", medium: "#1976d2", low: "#d32f2f" },
      });
      const scale = 1.5 * station.numEBikes / maxNumEBikes + .5;
      return <AdvancedMarker
        key={index}
        title={station.numEBikes.toString()}
        position={{ lat: station.location.lat, lng: station.location.lng }}
      >
        <Pin
          background={background}
          borderColor={background}
          glyphColor={background}
          scale={scale}
        />
      </AdvancedMarker>
    })}
  </Map >;
};

export default MapView;