import { formatCalculateDistance, calculateDistance } from "../lib/loc";
import { Station, Loc } from "../lib/types";
import { Badge, Divider, List, ListItem, ListItemButton, ListItemIcon, ListItemText } from "@mui/material";
import ElectricBikeIcon from '@mui/icons-material/ElectricBike';
import { getHighlightColor } from "../lib/colors";
import * as hacks from "../lib/hacks";

interface TableViewProps {
  stations: Station[];
  currentLocation: Loc;
  setCenter: (loc: Loc) => void;
}

const ListView: React.FC<TableViewProps> = ({ stations, currentLocation, setCenter }) => {
  const sortedStations = stations.sort((a, b) => calculateDistance(a.location, currentLocation) - calculateDistance(b.location, currentLocation));
  return <List
    sx={{
      py: 0,
      width: '100%',
      maxWidth: 360,
      overflow: "auto",
      maxHeight: hacks.listHeight,
      borderRadius: 2,
      border: '1px solid',
      borderColor: 'divider',
      backgroundColor: 'background.paper',
    }}>
    {sortedStations.map((station, index) => {
      const color = getHighlightColor({
        station,
        scale: { high: "success", medium: "primary", low: "error" },
      });
      const onClick = () => {
        console.log("onClick", station);
        setCenter(station.location);
      };
      const dist = `${formatCalculateDistance(station.location, currentLocation)} away`;
      return <>
        <ListItem key={`list-item-${station.id}`}>
          <ListItemButton onClick={onClick}>
            <ListItemIcon>
              <Badge badgeContent={station.numEBikes} color={color}>
                <ElectricBikeIcon />
              </Badge>
            </ListItemIcon>
            <ListItemText
              primary={station.name}
              secondary={`${dist} - (${station.location.lat.toFixed(4)}, ${station.location.lng.toFixed(4)})`} />
          </ListItemButton>
        </ListItem>
        {index < sortedStations.length - 1 &&
          <Divider key={`divider-${station.id}`} component="li" />}
      </>;
    })}
  </List>;
}

export default ListView;
